var API_ENDPOINT = "https://analytics.oceanengine.com/api/v2/conversion";
export function reportToDouyin(event_type, clickId, context) {
    return fetch(API_ENDPOINT, {
        method: "POST",
        mode: "no-cors",
        cache: "no-cache",
        credentials: "same-origin",
        headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
        },
        redirect: "follow",
        referrerPolicy: "no-referrer",
        body: JSON.stringify({
            event_type: event_type,
            context: {
                ad: {
                    callback: clickId,
                },
            },
            timestamp: Date.now(),
        }),
    });
}
